@font-face {
    font-family: 'Eurostile';
    src: local('Eurostile'), url(../../assets/fonts/Eurostile.ttf) format('truetype');
}
@font-face {
    font-family: 'EurostileBold';
    src: local('EurostileBold'), url(../../assets/fonts/EurostileBold.ttf) format('truetype');
}
@font-face {
    font-family: 'Apercu';
    src: local('Apercu'), url(../../assets/fonts/Apercu.ttf) format('truetype');
}


.home {
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    margin-top: 120px;
}
.home_header {
    font-family: 'Apercu';
    font-size: 40px;
    font-weight: 800;
    margin-bottom: 24px;
    text-align: center;
}
.home_text {
    font-family: 'Apercu';
    margin: 0;
    font-size: 20px;
    text-align: center;
    max-width: 400px;
    margin-bottom: 45px;
}
.home_image_wrapper {
    margin-bottom: 60px;
}
.home_image {
    border-radius: 50%;
}



